import { Fragment, useState, useEffect, useRef } from "react";

import classes from "./projects.module.css";
import Footer from "../components/core/footer";
import { apiRootURL } from "../apiConfigs";
import ProjectBanner from "../components/projectBanner";
import SearchIcon from "../assets/icons/search.png";
import SpinnerSmall from "../components/UI/spinner-small";

const Projects = () => {
  const [loading, setLoading] = useState(true);
  const [projects, setProjects] = useState([]);
  const [error, setError] = useState(false);

  const searchWord = useRef();
  let searchEmptyAndTouched = useRef();
  useEffect(() => {
    fetchProjects();
  }, [searchEmptyAndTouched]);

  const fetchProjects = () => {
    fetch(apiRootURL + "development/projects")
      .then((res) => {
        return res.json();
      })
      .then((jsonRes) => {
        const fetchedProjects = [...jsonRes.projects];
        return setProjects(() => {
          return [...fetchedProjects];
        });
      })
      .then(() => {
        setLoading(false);
      })
      .catch((err) => {
        setError(true);
        setLoading(false);
      });
  };

  const searchChangeHandler = () => {
    const searchBy = searchWord.current.value.trim().toUpperCase();
    if (searchBy === "") {
      setLoading(true);
      fetchProjects();
      return;
    }
    const restructuredProj = projects.map((project) => {
      return {
        ...project,
        technologies: project.technologies
          .map((tech) => {
            if (tech.name) {
              return tech.name;
            } else {
              return tech;
            }
          })
          .join(),
      };
    });
    const searchResults = restructuredProj.filter((proj) => {
      return proj.technologies.includes(searchBy) ? proj : undefined;
    });

    const finalResults = searchResults.map((res) => {
      return { ...res, technologies: res.technologies.split(",") };
    });

    setProjects(() => {
      return finalResults;
    });
  };

  let content = (
    <div>
      <div className={classes["projects-page"]}>
        <div className={classes["page-head"]}>
          <div className={classes["title"]}>Projects</div>
          <div className={classes["subtitle"]}>
            I bulid brands and create digital products to help my clients
            deliver unprecedented value.
          </div>
        </div>
        <div className={classes["search-container"]}>
          <input
            ref={searchWord}
            onChange={searchChangeHandler}
            className={classes["search-input"]}
            type="text"
            placeholder="Angular, Git, .."
          />
          <span className={classes["search-icon"]}>
            <img src={SearchIcon} alt="search" />
          </span>
        </div>
        <div className={classes["project-list"]}>
          {loading && <SpinnerSmall />}
          {!loading && projects.length > 0 &&
            projects.map((project) => {
              return <ProjectBanner key={project._id} project={project} />;
            })}
          {!loading && projects.length === 0 && (
            <div className={classes["empty-projects"]}>Cannot find projects with that Tech.</div>
          )}
        </div>
      </div>
      <Footer text={"Your next project!"} to={"/contact-me"} />
    </div>
  );

  if (error) {
    content = <div className={classes["error"]}>Something went wrong.</div>;
  }

  return <Fragment>{content}</Fragment>;
};

export default Projects;
