import { Link } from "react-router-dom"

import classes from "./projectBanner.module.css"
import { apiRootURL } from "../apiConfigs";



const ProjectBanner = (props) => {
    return (
        <Link className={classes["img-container"]} to={"/projects/" + props.project._id}>
          <div className={classes["upper-overlay"]}>
            <div className={classes["upper-overlay-content"]}>
              {props.project.title}
            </div>
          </div>
          <div className={classes["lower-overlay"]}>
            <div className={classes["lower-overlay-content"]}>
              {props.project.technologies.map((tech, i) => {
                return <span key={i} className={classes["tech-overlay"]}>{tech.name?tech.name:tech}</span>
              })}
            </div>
          </div>
          <img
            src={apiRootURL + props.project.mainImageUrl}
            alt={props.project.title}
          />
      </Link>
    )

}

export default ProjectBanner;